class Title extends Component {
    render() {
        return <div className="title" style={IsPcOrNot.get() ? {
            fontSize: "24px",
            fontWeight: "600",
            margin: "60px 0 20px 0"
        } : {
            fontSize: "16px",
            fontWeight: "600",
            margin: "40px 40px 20px 20px",
            textAlign: 'center',
            fontFamily: '方正黑简体'
        }}>
            {this.props.children}
        </div>
    }
}
class FormLine extends Component {
    constructor(props) {
        super(props);
        this.error = {
            text: "sure"
        }
    }
    handleInputChange = (key,event) => {
        // console.log("phone:"+this.props.phone);
        form[key] = event.target.value;
        this.setState(form);
    }
    componentDidMount() {
        let flag = true;
        this.valid = () => {
            if (this.props.valid != undefined) {
                let valids = this.props.valid.split("&&");
                for (let i = 0; i < valids.length; i++) {
                    if (valids[i] == 'notNull') {
                        if (form[this.props.name] == undefined || form[this.props.name].length == 0 || form[this.props.name] == '') {
                            this.error.text = "every_thing_is_notnull";
                            this.setState(this.error);
                            this.refs.Menu.open();
                            flag = false;
                            break;
                        }
                    }
                    // if (valids[i] == 'phone') {
                    //     if (!(/^1[34578]\d{9}$/.test(form[this.props.name]))) {
                    //         console.log(form[this.props.name] + '手机号错误');
                    //         this.error.text = "phone_is_wrong";
                    //         this.setState(this.error);
                    //         this.refs.Menu.open();
                    //         flag = false;
                    //     }
                    // }
                }
            }
            return flag;
        }
    }

    render() {
        return IsPcOrNot.get() ? <div style={{
            position: "relative",
            margin: (this.props.text == 'invoiceContent'?IsPcOrNot.get()?'0':'0 0 25px 0':'0 0 25px 0'),
            verticalAlign: "middle",
            display:"block"
        }}>
            <div>
                <Menu ref="Menu" text={this.error.text}/>
                <div style={{
                    position: "relative",
                    backgroundColor: "#fff",
                    whiteSpace: "nowrap",
                    border: "2px solid #444",
                    overflow: "hidden",
                    borderRadius: "4px",
                    width: this.props.text == 'code' ? "432px" : "600px",
                    height:this.props.text =='address' || this.props.text == 'companyAddress'?
                            '100px':'45px'
                }}>
                    <img style={(this.props.text =='address' || this.props.text == 'companyAddress')?{
                        textAlign: "left", height: "24px", verticalAlign: "middle", marginLeft: "20px",position:'absolute',top:'2px',left:0
                    }:{textAlign: "left", height: "24px", verticalAlign: "middle", marginLeft: "20px"
                    }}
                         src={this.props.icon}/>
                    {
                        (this.props.text =='address' || this.props.text == 'companyAddress')?
                            <textarea placeholder={messages[localStorage['language']][this.props.text]}
                                      name={this.props.name}
                                      value={form[this.props.name]}
                                      onChange={this.handleInputChange.bind(this,this.props.name)}
                                      style={{
                                          height:'100%',
                                          padding:'6px',
                                          border:0,
                                          fontSize:'12px',
                                          marginLeft: "80px",
                                          width: this.props.text == 'code' ? "369px" : "520px",
                                      }}
                                      readOnly={(this.props.name=='invoiceAmount' || this.props.name=='invoiceContent')?'readonly':''}>
                        </textarea>:
                            <input
                                placeholder={messages[localStorage['language']][this.props.text]}
                                style={{
                                    marginLeft: "40px",
                                    padding: "3px",
                                    width: this.props.text == 'code' ? "369px" : "520px",
                                    border: "0", height: "100%", fontSize: "12px",
                                    color:(this.props.name=='invoiceAmount' || this.props.name=='invoiceContent')?'#009edb':'#202020'

                                }}
                                readOnly={this.props.text == 'invoiceAmount' || this.props.text == 'invoiceContent'?'readonly':''}
                                onChange={this.handleInputChange.bind(this,this.props.name)}
                                type="text"
                                name={this.props.name}
                                value={this.props.name == 'invoiceAmount'?
                                     form[this.props.name]:this.props.name=='phone'?
                                     form[this.props.name]:this.props.name=="invoiceContent"?
                                     messages[localStorage['language']][this.props.text]:form[this.props.name]}/>

                    }
                </div>
            </div>
        </div> : <div style={{display: "block", width: "auto", verticalAlign: "49%", margin:this.props.text=='invoiceContent'?'0': "0 0 25px 0"}}>
            <div>
                <Menu ref="Menu" text={this.error.text}/>
                <div style={{
                    position: "relative",
                    backgroundColor: "#fff",
                    whiteSpace: "nowrap",
                    border: "2px solid #444",
                    overflow: "hidden",
                    borderRadius: "4px",
                    height:(this.props.text=='address'||this.props.text=='companyAddress')? "100px":"45px"
                }}>
                    <img style={{
                        textAlign: "left",
                        height: "24px",
                        verticalAlign: "middle",
                        margin:(this.props.text == 'address' || this.props.text=='compamyAddress')?'5px 0 0 10px':'0 0 0 10px'
                    }} src={this.props.icon}/>
                    {(this.props.text == 'address'||this.props.text == 'companyAddress')?
                        <textarea placeholder={messages[localStorage['language']][this.props.text]}
                                  name={this.props.name}
                                  value={form[this.props.name]}
                                  onChange={this.handleInputChange.bind(this,this.props.name)}
                                  style={{
                                      height:'100%',
                                      position:'absolute',
                                      left:'35px',
                                      padding:'5px',
                                      border:0,
                                      fontSize:'12px'
                                  }}
                        >
                        </textarea>:
                        <input placeholder={messages[localStorage['language']][this.props.text]}
                        style={{
                        marginLeft: IsPcOrNot.get() ? "40px" : "5px",
                        padding: "3px",
                        border: "0",
                        height: "100%",
                        fontSize: "12px",
                        color:(this.props.name=='invoiceAmount' || form['phone'] == this.props.phone || this.props.name=='invoiceContent')?'#009edb':'#202020'
                        }}
                               readOnly={this.props.text == 'invoiceAmount' || this.props.text == 'invoiceContent'?'readonly':''}
                               type="text"
                               onChange={this.handleInputChange.bind(this,this.props.name)}
                               name={this.props.name}
                               value={this.props.name == 'invoiceAmount'?
                                     form[this.props.name]:this.props.name=='phone'?
                                     form[this.props.name]:this.props.name=="invoiceContent"?
                                     messages[localStorage['language']][this.props.text]:form[this.props.name]}
                            />
                    }
                </div>
            </div>
        </div>
    }
}
class ImageLine extends Component {
    constructor(props) {
        super(props);
        form['scanningImgUrl'] = egbg;
        this.error = {
            text : 'sure'
        }
    }
    componentDidMount() {
        this.valid = () => {
            let $avatar = $("#avatar");
            if ($avatar.val() == undefined || $avatar.val() == '') {
                this.error.text = 'avatar_image_is_null';
                this.setState(this.error);
                this.refs.Menu.open();
                return false;
            } else {
                return true;
            }
        }
    }
    triggerUpload = () => {
        $('#avatar').click();
    };
    doUpload = () => {
        // let fileReader = new FileReader();
        // let file = document.getElementById('avatar');
        // fileReader.readAsDataURL(file.files[0]);
        // fileReader.onloadend = (event) => {
        //     let image = event.target.result;
        //     this.zip(image);
        // }
        let invoiceImg = new FormData($('#avatar-form')[0]);
        $.ajax({
            type : 'POST',
            url : Constant.url +'uploadInvoiceImg.do',
            data : invoiceImg,
            processData : false,
            contentType : false,
            beforeSend : () => {
                form['scanningImgUrl'] = Loading;
                this.setState(form);
            },
            success : (data) => {
                //TODO success callback
                if(data.error_no != '0') {
                    this.error.text = data.data;
                    this.setState(this.error);
                    this.refs.Menu.open();
                }
                else {
                    form['scanningImgUrl'] = data.data;
                    this.setState(form);
                }

            }
        })
    };
    render() {
        return <div>
            <Menu ref="Menu" text={this.error.text}/>
            <div style={{textAlign:"left",border:'2px solid rgb(68,68,68)',height:IsPcOrNot.get()?'220px':'5.4rem',width:IsPcOrNot.get()?'600px':'5.625rem'}}>
                <div style={{paddingLeft:'10px',height:'45px',
                    width:IsPcOrNot?"600px":'5.6rem',display:'flex',alignItems:"center",color:"#777",fontSize:'12px'}}>
                    <img src={sc} style={{height:"24px",marginRight:"5px"}} alt=""/>
                    <FormattedMessage id="smj"/>
                </div>
                <div style={{overflow:'hidden',float:"left",marginLeft:IsPcOrNot.get()?'33px':'',marginRight:IsPcOrNot.get()?'33px':""}}>
                    <div style={{float:IsPcOrNot.get()?'':'left',padding:'0 20px 0 10px',fontSize:"12px",display:IsPcOrNot.get()?'none':'block'}}>
                        <FormattedMessage id="example"/>
                    </div>
                    <img src={eg} alt="" style={{width:IsPcOrNot.get()?'245px':'3.375rem',display:"block",float:IsPcOrNot.get()?"":'left'}}/>
                    <div style={{float:IsPcOrNot.get()?'':'left',padding:'0 20px 0 10px',fontSize:"12px",display:IsPcOrNot.get()?"block":'none',textAlign:"center",paddingTop:'5px'}}>
                        <FormattedMessage id="example"/>
                    </div>
                </div>
                <div style={{float:"left"}}>
                    <form id="avatar-form" style={{ marginTop:IsPcOrNot.get()?'':'20px'}}>
                        <input id="avatar" onChange={this.doUpload} accept="image/*" type="file" style={{position:"absolute",visibility:"none",width:"0",height:"0"}} name="invoiceImg"/>
                        <div style={{float:'left',padding:'0 20px 0 10px',fontSize:'12px',display:IsPcOrNot.get()?'none':'block'}}>
                            <FormattedMessage id="upload"/>
                        </div>
                        <div onClick={this.triggerUpload} style={{
                            borderTop:"1px solid #dadada",
                            borderRight:"1px solid #dadada",
                            borderBottom:"1px solid #dadada",
                            display:IsPcOrNot.get()?'block':"inline-block",
                            width:IsPcOrNot.get()?"245px":"3.75rem",
                            height:IsPcOrNot.get()?"137px":"1.8rem",
                            borderLeft:"1px solid #dadada",
                            borderRadius:'5px',
                            backgroundPosition:"center",
                            backgroundImage:"url('"+form['scanningImgUrl']+"')",
                            backgroundRepeat:"no-repeat",
                            backgroundSize:form['scanningImgUrl'] == Photo?"35% auto":"contain",
                            verticalAlign:"top",
                            position:"relative",
                            cursor:"pointer",
                            fontSize:'12px'
                        }}>
                            {
                                form['scanningImgUrl'] == egbg?
                                    <div style={{
                                        textAlign:"center",
                                        width:"100%",
                                        position:"absolute",
                                        fontSize:"12px",
                                        color:"#777",
                                        bottom:"0",
                                        height:IsPcOrNot.get()?"50px":"20px"
                                    }}>
                                        <div style={{paddingRight:"5px"}}>
                                            <FormattedMessage id="click_to_upload1"/>
                                        </div>
                                    </div>:null
                            }
                        </div>
                        <div style={{float:IsPcOrNot.get()?'':'left',padding:'0 20px 0 10px',fontSize:'12px',display:IsPcOrNot.get()?'block':'none',textAlign:"center",paddingTop:'5px'}}>
                            <FormattedMessage id="upload"/>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    }
}
