class Form extends Component {
    constructor(props) {
        super(props);
        this.time = {
            "Second": 60
        }
        this.invoiceAmount=''
        this.phone=''
        this.selfphone=''
        this.invoiceContent = ''
        //companyTelephone  bank  bankAccount orderNumber scanningImgUrl
        //公司地址 公司电话 开户行 开户行帐号 扫描件 抬头 税号 总金额 开票内容 收件人姓名 收件人手机号 地址 邮编

        this.form_names = ['companyAddress','companyTelephone','bank', 'bankAccount', 'scanningImgUrl','invoiceTitle', 'taxNumber','invoiceAmount','invoiceContent','realName', 'phone', 'address', 'postCode']
        this.error = {
            "text": "sure"
        }
    }
    componentDidMount() {
        //获取金额
        $.get(Constant.url + 'queryInvoiceAmount.do',(data)=>{
            if(data.error_no == '0'){
                this.invoiceAmount=data.data
                this.setState({
                    invoiceAmount:this.invoiceAmount
                });
                form['invoiceAmount']=this.invoiceAmount
                this.setState(form)
            }
        });
        // //获取电话号码
        // $.get(Constant.url + 'hasLoginBaidu.do',(data)=>{
        //     if(data.error_no == '0') {
        //         if(data.data.phone !== undefined){
        //             this.selfphone=data.data.phone
        //             this.phone = data.data.phone
        //             this.setState({
        //                     phone:this.phone,
        //                     selfphone:this.selfphone,
        //                 }
        //             )
        //             form['phone']=this.phone
        //             this.setState(form)
        //         }
        //     }
        // });
        //如果已经提交过发票，将信息显示为最近一次提交的信息，方便更改
        $.get(Constant.url + 'queryInvoiceInfo.do',(data)=>{
            if(data.data !== undefined){
                if(data.error_no ==0){
                    form = data.data
                    // console.log(data.data)
                    form['scanningImgUrl'] = data.data.scanningImgUrl
                    this.setState(form)
                }
            }
        })
    }
    submit = () => {
        let flag = true;
        let _self = this;
        _self.form_names.every((i)=>{
            if (!_self.refs[i].valid()) {
                flag = false
                return false
            }
        })
        form.invoiceType=1;
        // console.log(form)
        if(flag) {
            form['createDate']= new Date();//去空用,
            $.post(Constant.url + 'saveInvoice.do',form,(data)=>{
                if(data.error_no == '0') {
                    //TODO success call back
                    this.error.text = 'noError'
                    this.setState(this.error)
                    this.refs.Menu.open()
                    // this.error.text = data.data;
                    // this.setState(this.error);
                    // this.refs.Menu.open();
                    // this.refs.Menu.redirect();
                }
                else {
                    this.error.text = data.data;
                    this.setState(this.error);
                    this.refs.Menu.open();
                }
            });
        }
    };
    // getphone(val) {
    //     if(val=='one') {
    //         form['phone']=''
    //         this.setState(form)
    //     }
    //     if(val=='two'){
    //         form['phone']=this.phone
    //         this.setState(form)
    //     }
    // }

    render() {
        return <div>
            <Title>
                <FormattedMessage id="companyInvoice"/>
            </Title>
            {/*{IsPcOrNot.get()?<div style={{width:"765px",padding:'20px 0 10px 0px'}}>*/}
                {/*<FormattedMessage id="cinvoice_tip1"/>*/}
                {/*<span style={{color:'red'}}>*/}
                    {/*<FormattedMessage id="cinvoice_tip2"/>*/}
                {/*</span>*/}
                {/*<FormattedMessage id="cinvoice_tip3"/>*/}
            {/*</div>:null}*/}
            <Menu ref="Menu" text={this.error.text}/>
            <table  className={IsPcOrNot.get() ? "form-table" : "wap-table"}>
                <tbody style={{margin:'0 auto'}} >
                <tr>
                    {
                        IsPcOrNot.get()?<td style={{paddingRight:'150px',paddingTop:'80px',textAlign:'left'}}>
                            <FormattedMessage id="invoice_info"/>
                        </td>:null

                    }
                    <td style={{padding:'28px 0 25px 0'}}>
                        <FormLine
                            ref={this.form_names[5]} icon={title}
                            text="invoiceTitle"
                            name={this.form_names[5]}
                            valid="notNull"/>
                        <FormLine
                            ref={this.form_names[6]} icon={taxNumber}
                            text="taxNumber"
                            name={this.form_names[6]}
                            valid="notNull"/>
                        <FormLine ref={this.form_names[7]} icon={amount} text="invoiceAmount" name={this.form_names[7]}
                                  valid="notNull"/>
                        <FormLine ref={this.form_names[8]} icon={Company} text="invoiceContent" name={this.form_names[8]}
                                  valid="notNull"/>
                    </td>
                </tr>
                <tr>
                    {
                        IsPcOrNot.get()?<td style={{paddingRight:'150px',textAlign:'left'}}>
                            <FormattedMessage id="company_info"/>
                        </td>:null
                    }
                    <td style={{padding:"0 0 25px 0"}}>
                        <div style={{textAlign: "left", verticalAlign: "middle"}}>
                            <FormLine
                                ref={this.form_names[0]}
                                text="companyAddress"
                                icon={caddress}
                                name={this.form_names[0]}
                                valid="notNull"/>
                        </div>
                        <FormLine ref={this.form_names[1]} icon={cphone} text="companyPhone" name={this.form_names[1]}
                                  valid="notNull"/>
                        <FormLine ref={this.form_names[2]} icon={bank} text="openingBank" name={this.form_names[2]}
                                  valid="notNull"/>
                        <FormLine ref={this.form_names[3]} icon={bankAccount} text="openingBank_account" name={this.form_names[3]}
                                  valid="notNull"/>
                        <ImageLine ref={this.form_names[4]}  text="card_picture" name={this.form_names[4]} valid="notNull"/>
                    </td>
                </tr>
                <tr>
                    {
                        IsPcOrNot.get()?<td style={{paddingRight:'150px'}}>
                            <FormattedMessage id="Addressee_info"/>
                        </td>:null
                    }
                    <td style={{padding:'0 0px 30px 0'}}>
                        {
                            IsPcOrNot.get()?null:<div style={{fontSize:'12px',textAlign:'center',padding:'15px 0',color:'#999'}}>
                                    <FormattedMessage id="Addressee_info"/>
                                </div>
                        }
                        <FormLine ref={this.form_names[9]} icon={Name} text="realName" name={this.form_names[9]}
                                  valid="notNull"/>
                        <FormLine ref={this.form_names[10]} icon={PhoneIcon} text="x_phone" name={this.form_names[10]}
                                  valid="notNull" phone={this.phone}/>
                        {/*<ChoicePhone getphone={this.getphone.bind(this)}/>*/}
                        <FormLine ref={this.form_names[11]} icon={caddress} text="address" name={this.form_names[11]}
                                  valid="notNull"/>
                        <FormLine ref={this.form_names[12]} icon={Email} text="postCode" name={this.form_names[12]}
                                  valid="notNull"/>
                    </td>
                </tr>
                </tbody>
            </table>
            <div style={{width: '5.6rem',fontSize:'14px', color:' rgb(102, 102, 102)',margin:IsPcOrNot.get()?'':'0 auto'}}>
                <FormattedMessage id="pcinvoice_tip"/>
                <span style={{color:'red'}}>
                <FormattedMessage id="cinvoice_tip2"/>
            </span>
                <FormattedMessage id="pcinvoice_tip2"/>
            </div>
            <div style={{margin: "40px 0 80px 0", textAlign: "center"}}>
                <button type="submit" className="base_type_btn"
                        style={{padding: "15px 80px", borderRadius: "3px", marginTop: "40px"}} onClick={this.submit}>
                    <FormattedMessage id="submit"/>
                </button>
            </div>
        </div>

    }
}
class Phone extends Component {
    render() {
        return <div style={{backgroundColor: "#ffffff"}}>
            <Header/>
            <Form/>
            <Footer/>
        </div>
    }
}
class PC extends Component {
    render() {
        return <div style={{backgroundColor: "#ffffff"}}>
            <Header/>
            <div style={{margin: "0 auto", display: "block", position: "relative", width: "1200px"}}>
                <Form/>
            </div>
            <Footer/>
        </div>
    }
}
export class Invitation extends Component {
    constructor(props) {
        super(props);
    }

    changeLanguage = () => {
        if (localStorage['language'] == 'en') {
            localStorage['language'] = 'zh-CN';
        }
        else {
            localStorage['language'] = 'en';
        }
        location.href = location.href;
    };

    render() {
        return <div>
            {
                IsPcOrNot.get() ?
                    <PC/> : <Phone/>
            }
        </div>
    }
}
export default Invitation





